import React, { Component } from 'react';
import { Carousel } from 'antd';
import Floor from "./floor"
import './home.scss'
class Home extends Component{
  render(){
    return(
      <div className="container">
        <Carousel autoplay>
          <img src={require('@/static/image/carousel_1.jpg')} alt=""></img>
          <img src={require('@/static/image/carousel_1.jpg')} alt=""></img>
          <img src={require('@/static/image/carousel_1.jpg')} alt=""></img>
          <img src={require('@/static/image/carousel_1.jpg')} alt=""></img>
        </Carousel>
        <Floor></Floor>
      </div>
    )
  }

}

export default Home