import React, { Component } from 'react';
import './index.scss'
class Floor extends Component{
  constructor(props){
    super(props)
  }
  render(){
    return(
      <div className="floor-wrap">
        {
          [1,2,3,4].map((item,index)=>{
          return (
            <div className="floor-item" key={index}>
              <div className="title-wrap">
                <span className="floor-title">
                  <span className="floor-name">蓄电池</span>
                  <i className="text-border"></i>
                </span>
              </div>
              <div className="floor-goods">
                <img className="special-img" src="" alt=""/>
                <ul className="goods-wrap">
                  {
                    [1,2,3,4,5,6,7,8].map((item=>{
                      return (
                        <li className="goods-item">
                          <img className="goods-img" src="" alt=""/>
                        </li>
                      )
                    }))
                  }
                </ul>
              </div>
            </div>
          )
        })
        }
      </div>
    )
  }
}
export default Floor